import * as React from 'react';
import { graphql } from 'gatsby';
import Layout from '~src/components/Layout/Layout';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
import { PostFeed } from '~src/components/Sections/Posts/PostFeed/PostFeed';
import { News as NewsData } from '~src/pages/__generated__/News';

const FilmographyHero = () => {
	const { formatMessage } = useIntlHelper();
	return (
		<header className="site-archive-header">
			<div className="inner site-header-content">
				<h1 className="site-title"> {formatMessage('navigation.news')}</h1>
				{/* <h2 className="site-description">{formatMessage('navigation.filmography')}</h2> */}
			</div>
		</header>
	);
};

const NewsPage: React.FC<{ data: NewsData }> = ({ data }) => {
	const { formatMessage } = useIntlHelper();
	return (
		<>
			<Layout template="news" header={<FilmographyHero />}>
				<div className="inner">
					<h2> {formatMessage('navigation.news')}</h2>

					<PostFeed data={data} />
				</div>
			</Layout>
		</>
	);
};
export default NewsPage;

export const NewsPageQuery = graphql`
	query News($locale: String!) {
		posts: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }

			filter: { fields: { locale: { eq: $locale }, category: { eq: "posts" } }, frontmatter: { draft: { ne: true } } }
		) {
			nodes {
				fields {
					slug
					category
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title
					featured
					tags
					image {
						childImageSharp {
							gatsbyImageData(layout: FULL_WIDTH)
						}
					}
				}
				excerpt(truncate: true, pruneLength: 200)
				timeToRead
			}
		}
	}
`;
