import * as React from 'react';
import { Index } from '~src/pages/__generated__/Index';
import { News } from '~src/pages/__generated__/News';
import { NotFound } from '~src/pages/__generated__/NotFound';
import { Tag } from '~src/templates/__generated__/Tag';
import { Post } from '~src/types/Post';
import Link from '~src/_i18n/components/Link';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
import PostCard from '../PostCard/PostCard';
import * as styles from './PostFeed.module.scss';

export const PostFeed: React.FC<{ data: Index | NotFound | Tag | News; isHome?: boolean }> = ({ data, isHome }) => {
	const { formatMessage } = useIntlHelper();
	return (
		<div className={styles.posts}>
			<div className={styles.postFeed}>
				{(data.posts.nodes as Post[]).map((post: Post, i: number) => {
					return <PostCard post={post} i={i} key={i} isHome={isHome} />;
				})}
			</div>
			<Link to="/news">
				<h3 className={styles.readMore}> {isHome ? formatMessage('general.allNews') : ''}</h3>
			</Link>
		</div>
	);
};
