import * as React from 'react';
//import { Index_posts_nodes as Post } from '~src/pages/__generated__/Index';
//import AuthorList from '~src/components/Sections/AuthorList/AuthorList';
import Link from '~src/_i18n/components/Link';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
import ImgSharp from '~src/components/utils/ImgSharp/ImgSharp';
import * as styles from './PostCard.module.scss';
import { Post } from '~src/types/Post';

const PostCard: React.FC<{
	post: Post;
	i?: number;
	isHome?: boolean;
}> = ({ post, i, isHome }) => {
	const { intl, formatDate, formatMessage } = useIntlHelper();
	const isFeatured = post.frontmatter.featured;
	return (
		<article className={styles.postCard + ` ` + ((isHome && isFeatured && 0 === i % 6 && styles.postCardLarge) || ``)}>
			<Link className={styles.postCardImageLink} to={`/${post.fields.category}/${post.fields.slug}`}>
				<ImgSharp
					className={styles.postCardImage}
					alt={post.frontmatter.title}
					/* 	sizes="(max-width: 1000px) 400px, 700px" */
					image={post.frontmatter.image.childImageSharp.gatsbyImageData}
				/>
			</Link>
			<div className={styles.postCardContent}>
				<Link className={styles.postCardContentLink} to={`/${post.fields.category}/${post.fields.slug}`}>
					<header className={styles.postCardHeader}>
						<div className={styles.postCardPrimaryTag}>
							{post.frontmatter.tags[0] !== undefined &&
								intl.formatMessage({
									id: `tags.${post.frontmatter.tags[0]}.title`,
								})}
						</div>
						<h2 className={styles.postCardTitle}>{post.frontmatter.title}</h2>
					</header>
					<section className={styles.postCardExcerpt}>
						<p>{post.excerpt}</p>
					</section>
				</Link>
				<footer className={styles.postCardMeta}>
					{/* 	<AuthorList authors={post.frontmatter.authors} /> */}

					<div className={styles.postCardBylineContent}>
						<span>
							{/* 	<Link to={`/authors/${post.frontmatter.authors[0].id}/`}>
								{intl.formatMessage({
									id: `authors.${post.frontmatter.authors[0].id}.title`,
								})}
							</Link> */}
						</span>
						<span className={styles.postCardBylineDate}>
							<time dateTime={post.frontmatter.date}>{formatDate(post.frontmatter.date)}</time> <span className="bull">â€¢</span>{' '}
							{intl.formatNumber(post.timeToRead)} {formatMessage('posts.timeToRead')}
						</span>
					</div>
				</footer>
			</div>
		</article>
	);
};

export default PostCard;
